import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

/**
 * Avatar component
 */
export default function Avatar(props) {
  const {
    letters,
    className,
    size,
    src,
    defaultSrc,
    style,
    bgType,
    color,
    textColor,
    onError, // eslint-disable-line
    ...other
  } = props;

  /**
   * If component's prop src is empty and prop letters have value
   * render
   * @return {ReactElement} markup
   */
  if (letters && !src) {
    /**
     * @return {ReactElement} markup
     */
    return (
      <div
        data-component="avatar"
        className={classNames(
          'avatar',
          'round_circle',
          'truncate_text',
          {
            [`fill_${color}`]: bgType === 'fill',
          },
          [`stroke_${color}`],
          [`text_${textColor}`],
          className,
        )}
        style={{
          ...style,
          height: size,
          width: size,
          lineHeight: `${size - 2}px`,
        }}
        {...other}
      >
        {letters[0]}
      </div>
    );
  }

  /**
   * @return {ReactElement} markup
   */
  return (
    <img
      data-component="avatar"
      src={src || defaultSrc}
      className={classNames(
        'avatar',
        'round_circle',
        {
          [`fill_${color}`]: color,
        },
        className,
      )}
      style={{
        ...style,
        height: size,
        width: size,
      }}
      onError={(e) => { e.target.src = defaultSrc; }}
      {...other}
    />
  );
}

Avatar.propTypes = {
  /**
   * Can be used, for instance, to render a letter inside the avatar
   */
  letters: PropTypes.string,
  /**
   * The css class name of the root div or img element
   */
  className: PropTypes.string,
  /**
   * This is the size of the avatar in pixels
   */
  size: PropTypes.number,
  /**
   * If passed in, this component will render an img element. Otherwise, a div will be rendered
   */
  src: PropTypes.string,
  /**
   * If `src` passed and image not loaded - the image src will defaultSrc
   */
  defaultSrc: PropTypes.string,
  /**
   * Override the inline-styles of the root element
   */
  style: PropTypes.oneOfType([
    PropTypes.object,
  ]),
  /**
   * Component type one of `fill` or `stroke`.
   * If `fill` - component will be have background-color from `color` props.
   * If `stroke` - component will be have border-color from `color` props.
   */
  bgType: PropTypes.oneOf(['fill', 'stroke']),
  /**
   * Component color from theme
   */
  color: PropTypes.string,
  /**
   * Component text color from theme
   */
  textColor: PropTypes.string,
};

Avatar.defaultProps = {
  letters: '',
  className: '',
  size: 40,
  src: '',
  defaultSrc: 'data:image/png;base64,',
  style: {},
  bgType: 'fill',
  color: 'light_grey',
  textColor: 'primary',
};
